/* ==== BASE STYLES ====*/


/* ==================== GLOBAL STYLES ======================= */

@import url("https://rsms.me/inter/inter.css");
html {
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	font-size: 15px;
}


/* ========================
INCLUDE STYLES AND COMPONENTS
===================== */


/* ================== HELPERS  =================== */

a:hover {
	text-decoration: none;
}

.img-wrap {
	text-align: center;
	display: block;
}

.img-wrap img {
	max-width: 100%;
}

.icon-wrap {
	text-align: center;
	display: inline-block;
}

.price-wrap {
	letter-spacing: -0.7px;
}

.content-body {
	-webkit-box-flex: 1;
	-ms-flex: 1 1 auto;
	flex: 1 1 auto;
	padding: 2rem 2.3rem;
}


/* =================  DEFINED VARIABLES =================== */

.icon-xs,
.icon-sm,
.icon-md,
.icon-lg {
	display: inline-block;
	text-align: center;
	-ms-flex-negative: 0;
	flex-shrink: 0;
	-webkit-box-flex: 0;
	-ms-flex-positive: 0;
	flex-grow: 0;
}

.icon-xs {
	width: 32px;
	height: 32px;
	line-height: 32px !important;
	font-size: 16px;
}

.icon-sm {
	width: 48px;
	height: 48px;
	line-height: 48px !important;
	font-size: 20px;
}

.icon-md {
	width: 60px;
	height: 60px;
	line-height: 60px !important;
	font-size: 28px;
}

.icon-lg {
	width: 96px;
	height: 96px;
	line-height: 96px !important;
	font-size: 42px;
}

.img-xs,
.img-sm,
.img-md,
.img-lg {
	-o-object-fit: cover;
	object-fit: cover;
	-ms-flex-negative: 0;
	flex-shrink: 0;
	-webkit-box-flex: 0;
	-ms-flex-positive: 0;
	flex-grow: 0;
}

.img-xs {
	width: 40px;
	height: 40px;
}

.img-sm {
	width: 80px;
	height: 80px;
}

.img-md {
	width: 140px;
	height: 140px;
}

.img-lg {
	width: 250px;
	height: 250px;
}

.center-xy {
	top: 50%;
	left: 50%;
	position: absolute;
	-webkit-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}

.center-y {
	top: 50%;
	position: absolute;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
}

.center-x {
	left: 50%;
	position: absolute;
	-webkit-transform: translateX(-50%);
	transform: translateX(-50%);
}

.vh-100 {
	height: 100vh;
}

.overflow-h {
	overflow: hidden;
}

.flip-h {
	-webkit-transform: scaleX(-1);
	transform: scaleX(-1);
}

.opacity {
	opacity: .5;
}

[class*="hover-"] {
	-webkit-transition: .5s;
	transition: .5s;
}

.hover-bg:hover {
	background: #3167eb;
	/* For browsers that do not support gradients */
	/* For Safari 5.1 to 6.0 */
	/* For Opera 11.1 to 12.0 */
	/* For Firefox 3.6 to 15 */
	background: -webkit-gradient(linear, left top, right bottom, from(#3167eb), to(#3167eb));
	background: linear-gradient(to bottom right, #3167eb, #3167eb);
	/* Standard syntax */
	-webkit-transition: .5s;
	transition: .5s;
}

.hover-zoom:hover {
	-webkit-transition: .5s;
	transition: .5s;
	-webkit-transform: scaleX(1);
	transform: scaleX(1);
}


/* ============== spacing ============== */

.padding-y {
	padding-top: 40px;
	padding-bottom: 40px;
}

.padding-top {
