.bg-pattern {
	background-image: linear-gradient(135deg, #3167eb, #545454);
	background-size: cover, auto;
	background-repeat: no-repeat, repeat;
	background-position: 0px 0px, 50% 50%;
}

.bg-img {
	overflow: hidden;
	background-color: #ddd;
	background-image: linear-gradient(135deg, rgba(49, 103, 235, 0.8), rgba(84, 84, 84, 0.9));
	background-position: 0px 0px, 50% 50%;
	background-size: auto cover;
}

.overlay-gradient {
	position: relative;
}

.overlay-gradient:before {
	position: absolute;
	content: "";
	display: block;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	opacity: .6;
	-webkit-transition: .5s;
	transition: .5s;
	background: aqua;
	/* For browsers that do not support gradients */
	/* For Safari 5.1 to 6.0 */
	/* For Opera 11.1 to 12.0 */
	/* For Firefox 3.6 to 15 */
	background: -webkit-gradient(linear, left top, right bottom, from(#3167eb), to(aqua));
	background: linear-gradient(to bottom right, #3167eb, aqua);
	/* Standard syntax */
}

.overlay-gradient:hover:before {
	opacity: .9;
}


