/* -------------------- list.row--------------------- */

ul.row,
ul.row-sm {
	list-style: none;
	padding: 0;
}


/* ================= lists ================= */

[class*="list-"] li:after {
	visibility: hidden;
	display: block;
	content: "";
	overflow: hidden;
	height: 0;
	clear: both;
}

.list-icon {
	list-style: none;
	padding-left: 0;
}

.list-icon li {
	margin-bottom: 7px;
	position: relative;
	padding-left: 30px;
}

.list-icon .icon {
	width: 22px;
	margin-right: 15px;
	vertical-align: middle;
	text-align: center;
	color: #545454;
	position: absolute;
	top: 3px;
	left: 0;
}

.list-icon span {
	vertical-align: middle;
}

.list-bullet {
	list-style: none;
	padding-left: 0;
}

.list-bullet li {
	margin-bottom: 5px;
	position: relative;
	padding-left: 15px;
}

.list-bullet li::before {
	content: " ";
	position: absolute;
	top: 8px;
	left: 0;
	width: 7px;
	border-radius: 100%;
	height: 7px;
	background: #ced4da;
}

.list-check {
	padding-left: 0;
	list-style: none;
}

.list-check>li {
	position: relative;
	padding-left: 24px;
	margin-bottom: 7px;
}

.list-check>li::before {
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	font-weight: 900;
	font-size: 12px;
	color: #00b517;
	position: absolute;
	top: 2px;
	left: 0;
	content: "\f00c";
}

.list-normal {
	list-style: none;
	padding-left: 0;
}

.list-normal li {
	padding-left: 1em;
	margin-bottom: 7px;
}

.list-normal li:before {
	content: "\2022";
	color: #3167eb;
	display: inline-block;
	width: 1em;
	margin-left: -1em;
}

.list-menu {
	list-style: none;
	margin: 0;
	padding-left: 0;
}

.list-menu li {
	margin-bottom: 5px;
}

.list-menu a {
	color: #343a40;
}

.list-menu a:hover {
	color: #3167eb;
}

.cols-two {
	-webkit-column-count: 2;
	column-count: 2;
}

.cols-three {
	-webkit-column-count: 3;
	column-count: 3;
}

.cols-four {
	-webkit-column-count: 4;
	column-count: 4;
}


/* IF CARD IS LINKED */

a[class*='card'] {
	color: initial;
}

a[class*='card']:hover .title {
	color: #3167eb;
}


/* BASE STYLE FOR PRODUCT ITEM */

[class*='card-product'] a.title {
	color: #212529;
	display: block;
}

[class*='card-product'] a.title:hover {
	color: #3167eb;
}

[class*='card-product'] .badge {
	top: 10px;
	left: 10px;
	position: absolute;
}

[class*='card-product'] p {
	margin-bottom: 0;
}

[class*='card-product'] .img-wrap {
	overflow: hidden;
	position: relative;
}

[class*='card-product'] .img-wrap img {
	height: 100%;
	max-width: 100%;
	width: auto;
	display: inline-block;
	-o-object-fit: cover;
	object-fit: cover;
}


/* GRID STYLE PRODUCT ITEM */

.card-product-grid {
	margin-bottom: 20px;
}

.card-product-grid .img-wrap {
	border-radius: 0.2rem 0.2rem 0 0;
	height: 220px;
}

.card-product-grid .info-wrap {
	overflow: hidden;
	padding: 18px 20px;
}

.card-product-grid .bottom-wrap {
	padding: 18px;
	border-top: 1px solid #e4e4e4;
}

.card-product-grid .topbar {
	position: absolute;
	top: 0;
	width: 100%;
	z-index: 10;
	left: 0;
	text-align: initial;
	padding: 10px 15px;
}

.card-product-grid .topbar a {
	color: #ccc;
}

.card-product-grid .topbar a:hover {
	color: #3167eb;
}

.card-product-grid .fix-height {
	height: 80px;
	overflow: hidden;
}

.card-product-grid .btn-overlay {
	-webkit-transition: .5s;
	transition: .5s;
	opacity: 0;
	left: 0;
	bottom: 0;
	color: #fff;
	width: 100%;
	padding: 5px 0;
	text-align: center;
	position: absolute;
	background: rgba(0, 0, 0, 0.5);
}

.card-product-grid:hover .btn-overlay {
	opacity: 1;
}

.card-product-grid:hover {
	-webkit-box-shadow: 0 4px 15px rgba(153, 153, 153, 0.3);
	box-shadow: 0 4px 15px rgba(153, 153, 153, 0.3);
	-webkit-transition: .3s;
	transition: .3s;
}

.item-slide .card-product-grid {
	margin-bottom: 0;
}


/* LARGE LIST STYLE PRODUCT ITEM */

.card-product-list {
	margin-bottom: 20px;
}

.card-product-list .img-wrap {
	height: 220px;
	border-radius: 0.37rem 0 0 0.37rem;
}

.card-product-list .info-main {
	padding: 1.5rem 1rem;
}

.card-product-list .info-aside {
	padding: 1.5rem 1rem;
	border-left: 1px solid #e4e4e4;
	height: 100%;
}

.card-sm .img-wrap {
	height: 160px;
}

.card-lg .img-wrap {
	height: 280px;
}

.card-banner {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	position: relative;
	overflow: hidden;
	background-color: #fff;
	background-size: cover;
	background-position: center center;
	border-radius: 0.37rem;
}

.card-banner .card-body {
	background-size: cover;
	position: relative;
	z-index: 10;
}

.card-banner .caption {
	background-color: rgba(0, 0, 0, 0.65);
	z-index: 10;
	padding: 1.25rem;
	color: #fff;
}

.card-banner .caption.bottom {
	bottom: 0;
	left: 0;
	position: absolute;
	width: 100%;
}

.card-banner .caption.top {
	top: 0;
	left: 0;
	position: absolute;
	width: 100%;
}

.card-banner .caption.left {
	top: 0;
	left: 0;
	bottom: 0;
	position: absolute;
}

.card-banner .text-bottom {
	position: absolute;
	left: 0;
	bottom: 0;
	left: 0;
	z-index: 10;
	width: 100%;
	padding: 7px 20px;
	padding-top: 30px;
	border-radius: 0 0 0.37rem 0.37rem;
