/* ================= RATINGS ============== */

.label-rating {
	margin-left: 7px;
	display: inline-block;
	vertical-align: middle;
}


/* rating-list */

.rating-stars {
	display: inline-block;
	vertical-align: middle;
	list-style: none;
	margin: 0;
	padding: 0;
	position: relative;
	white-space: nowrap;
	clear: both;
}

.rating-stars i {
	font-size: 14px;
	color: #ccc;
	display: inline;
}

.rating-stars li {
	display: block;
	text-overflow: clip;
	white-space: nowrap;
	z-index: 1;
}

.rating-stars li.stars-active {
	z-index: 2;
	position: absolute;
	top: 0;
	left: 0;
	overflow: hidden;
}

.rating-stars li.stars-active i {
	color: orange;
}


/* ================= TRACKING ============== */

.tracking-wrap {
	position: relative;
	background-color: #ddd;
	height: 7px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin-bottom: 60px;
	margin-top: 50px;
}

.tracking-wrap .step {
	-webkit-box-flex: 1;
	-ms-flex-positive: 1;
	flex-grow: 1;
	width: 25%;
	margin-top: -18px;
	text-align: center;
	position: relative;
}

.tracking-wrap .step::before {
	height: 7px;
	position: absolute;
	content: "";
	width: 100%;
	left: 0;
	top: 18px;
}

.tracking-wrap .icon {
	display: inline-block;
	width: 40px;
	height: 40px;
	line-height: 40px;
	position: relative;
	border-radius: 100%;
	background: #ddd;
}

.tracking-wrap .text {
	display: block;
	margin-top: 7px;
}

.tracking-wrap .step.active .icon {
	background: #3167eb;
	color: #fff;
}

.tracking-wrap .step.active .text {
	font-weight: 400;
	color: #000;
}

.tracking-wrap .step.active:before {
	background: #3167eb;
}

.menu-category {
	margin: 0;
	padding: 0;
}

.menu-category li {
	position: relative;
	display: block;
	border-bottom: 1px solid #e4e4e4;
}

.menu-category li:last-child {
	border-bottom: 0;
}

.menu-category a {
	color: #212529;
}

.menu-category>li>a {
	display: block;
	padding: 10px 18px;
}

.menu-category .submenu {
	display: none;
	margin: 0;
	padding: 0;
	-webkit-box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.2);
	box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.2);
	z-index: 20;
	position: absolute;
	left: 100%;
	top: 0;
	width: 250px;
	background: #fff;
}

.menu-category .submenu li {
	list-style: none;
}

.menu-category .submenu li a {
	padding: 10px 16px;
	display: block;
}

.menu-category li:hover {
	background: #f8f9fa;
	-webkit-transition: all 0.3s;
	transition: all 0.3s;
}

.menu-category li:hover .submenu {
	display: block;
	margin-left: 0px;
}

.menu-category .has-submenu>a:after {
	content: "\203A";
	font-size: 24px;
	color: #999;
	line-height: 18px;
	font-weight: bold;
	float: right;
}

.form-inline input[type='number'] {
	max-width: 100px;
}

.input-spinner {
	width: 120px;
	-ms-flex-wrap: nowrap;
	flex-wrap: nowrap;
	display: -webkit-inline-box;
	display: -ms-inline-flexbox;
	display: inline-flex;
	vertical-align: middle;
}

.input-spinner input.form-control {
	text-align: center;
	max-width: 46px;
	-ms-flex-preferred-size: 46px;
	flex-basis: 46px;
	border-color: #e4e4e4;
	-webkit-box-flex: 0;
	-ms-flex-positive: 0;
	flex-grow: 0;
}

.input-spinner .btn {
	width: 36px;
	text-align: center;
}

.checkbox-btn {
	position: relative;
}

.checkbox-btn input {
	position: absolute;
	z-index: -1;
	opacity: 0;
}

.checkbox-btn input:checked~.btn {
	border-color: #3167eb;
	background-color: #3167eb;
	color: #fff;
}


